import { selector } from 'recoil';
import {myMBTI, myState, selectedMBTI} from "../state/state";
import {getMaxKey} from "../../contants";

export const mySelector = selector({
    key: 'mySelector', //    
    get: ({ get }) => {
        const value = get(myState); //    
        return value.toUpperCase(); //   
    },
});

type MBTI =
'E'|
'I'|
'S'|
'N'|
'T'|
'F'|
'J'|
'P';
export const myMBTISelector = selector({
    key: 'myMBTISelector', //    
    get: ({ get }) => {
        const value = get(myMBTI); //    
        // @ts-ignore
        return value.toUpperCase(); //   
    },
});
export const selectedMBTISelector = selector({
    key: 'selectedMBTISelector', //    
    get: ({ get }) => {
        const value = get(selectedMBTI); //    
        // @ts-ignore
        const r = value.split('').reduce((acc,cur:MBTI)=>{
            return {
                ...acc,
                [cur]: acc[cur]+1
            }
        }, {
            E:0,
            I:0,
            S:0,
            N:0,
            T:0,
            F:0,
            J:0,
            P:0,
        })
        let res = '';
        res+=getMaxKey('E','I', r);
        res+=getMaxKey('S','N', r);
        res+=getMaxKey('T','F', r);
        res+=getMaxKey('J','P', r);
        console.log(r, res)
        return res.toUpperCase(); //   
    },
});