import React, {useEffect} from 'react';
import {Link, useNavigate, useParams} from "react-router-dom";
import {useRecoilValue, useSetRecoilState} from "recoil";
import {myState, selectedMBTI} from "../../recoil/state/state";
import {myMBTISelector, mySelector} from "../../recoil/selector/selector";
import Box from "@mui/material/Box";
import {Button} from "@mui/material";
import Container from "@mui/material/Container";
import {COMMON_QUESTIONS, results} from "../../contants";


export const Test = () => {
    const params = useParams();
    const setValue = useSetRecoilState(myState);

    const computedValue2 = useRecoilValue(myMBTISelector);


    const navigate = useNavigate();

    const setSelectedMBTI = useSetRecoilState(selectedMBTI);
    const {id}:any = params
    const question = COMMON_QUESTIONS[parseInt(id)-1]
    const handleAnswerClick = (index:any) => {
        const idx = parseInt(id);
        let nextLink = `/test/${idx+1}`
        if(idx === COMMON_QUESTIONS.length) {
            nextLink = '/result'
        }
        // console.log(results[idx-1][index],nextLink)
        setSelectedMBTI((state:any)=>state+question.type[index])
        navigate(nextLink)
    }
    useEffect(()=>{
        if(!computedValue2) {
            navigate('/')
        }
    },[])
    return (
        <>
            <Container maxWidth="md">
                <Box sx={{height: '100vh', paddingTop: '6rem', display: 'flex', flexDirection: 'column'}}>
                    {/*   */}
                    <div className='index' style={{color:'#6c6c72', marginBottom:'1rem'}}>
                        {params.id}/{COMMON_QUESTIONS.length}
                    </div>
                    {/*  */}
                    <div className='question' style={{marginBottom:'4rem', color:'#19f7d0'}}>
                        Q. {question.question}
                    </div>
                    <div style={{display:'flex', flexDirection:'row'}}>
                        <div style={{border:'solid 1px #6c6c72', background:'#6c6c72', borderRadius:'6px', flex:1, marginRight:'8px',fontSize:'24px', cursor:'pointer', padding:'16px 8px 16px 8px'}} onClick={()=>handleAnswerClick(0)}>
                                {question.answers[0]}
                        </div>
                        <div style={{border:'solid 1px #6c6c72', background:'#6c6c72', borderRadius:'6px',flex:1, cursor:'pointer',padding:'16px 8px 16px 8px',fontSize:'24px',}} onClick={()=>handleAnswerClick(1)}>
                            {question.answers[1]}
                        </div>
                    </div>
                </Box>
            </Container>

        </>
    );
};
