import React, {useEffect, useState} from 'react';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import {useRecoilValue, useSetRecoilState} from "recoil";
import {myMBTI, myState, selectedMBTI} from "../recoil/state/state";
import {
    Button,
    FormControl, FormControlLabel,
    FormGroup,
    InputLabel,
    MenuItem,
    Radio,
    Select,
    SelectChangeEvent, Switch,
    Typography
} from '@mui/material';
import {Link} from "react-router-dom";
import titleLog from "../static/img/title_final.png"
import {make_questions, MBTI, MEMBERS} from "../contants";
import {myMBTISelector} from "../recoil/selector/selector";

export const Main = () => {
    const value = useRecoilValue(myState);
    const [age, setAge] = React.useState('');
    const setMyMBTI = useSetRecoilState(myMBTI);
    const setSelectedMBTI = useSetRecoilState(selectedMBTI);
    const computedMyMBTI = useRecoilValue(myMBTISelector);
    const [isKbds, setIsKbds] = useState(true);
    const handleChange = (event: SelectChangeEvent) => {
        setMyMBTI(event.target.value as string);
    };
    useEffect(()=>{
        setMyMBTI('');
        setSelectedMBTI('');
        make_questions()
    },[])
    const handleSwitchChange = () => {
        setIsKbds((state)=> !state);
    }
    return (
        <>
            <Container maxWidth="md">
                <Box sx={{ height: '100vh', paddingTop:'4rem', display:'flex', flexDirection:'column' }}>
                    <div>
                        <img src={titleLog} alt="ddd" width={500} height={300}/>
                    </div>
                    <div>
                        <div style={{marginBottom:'2rem'}}>
                            <Typography>
                                  MBTI .
                            </Typography>
                        </div>
                        <div style={{alignItems:'center', display:'flex',justifyContent:'center'}}>
                            <FormGroup>
                                <FormControlLabel control={<Switch checked={isKbds} onChange={handleSwitchChange} />} label="For KBDS" />
                            </FormGroup>
                        </div>
                        <FormControl sx={{width:'300px', fontSize:'30px'}}>
                            <InputLabel id="demo-simple-select-label">{isKbds ? "Member":"MBTI"}</InputLabel>
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={computedMyMBTI}
                                label=" MBTI"
                                onChange={handleChange}
                            >
                                {isKbds ? MEMBERS.map((member)=>
                                    <MenuItem value={member.type}>{member.name}</MenuItem>
                                ):Object.keys(MBTI).map((key)=>
                                    <MenuItem value={key}>{key}</MenuItem>
                                )}
                            </Select>
                        </FormControl>
                    </div>
                    <div style={{marginTop:'2rem'}}>
                        <Link to={'/test/1'}>
                            <Button variant="outlined" size="large" sx={{width:'300px', fontSize:'30px'}} color="salmon"></Button>
                        </Link>
                    </div>
                </Box>
            </Container>
        </>
    );
};
